import classes from './Messages.module.css';
import DialogItem from "./DialogItem/DialogItem";
import Message from "./MessageItem/MessagesDialog";
import React from "react";

const Messages = (props) => {
    let dialogsElements = props.state.dialogsData.map( dialog => <DialogItem name={dialog.name} id={dialog.id}/>)
    let messagesElements = props.state.messagesData.map( messageDialog => <Message message={messageDialog.message} />)

    let newMessagesElement = React.createRef();

    const addMessage = () => props.dispatch({ type: 'ADD-MESSAGE' });

    const onMessageChange = () => {
        let text = newMessagesElement.current.value;
        props.dispatch({ type: 'UPDATE-NEW-MESSAGE-TEXT', newMessage: text });
    }

    return (
        <div>
            <div className={classes.dialogs}>
                <div className={classes.dialogItems}>
                    { dialogsElements }
                </div>
                <div className={classes.messages}>
                    { messagesElements }
                </div>
            </div>
            <div className={classes.textarea}>
                <div><textarea onChange={onMessageChange} ref={ newMessagesElement } value={props.newMessageText}/></div>
                <div><button className={classes.addMessage} onClick={addMessage}>Add Message</button></div>
            </div>
        </div>
    )
};

export default Messages;