let store = {
    _state: {
        profilePage: {
            postsData: [
                {id: 1, message: 'hi, how are you?', likesCount: '15'},
                {id: 2, message: 'Hi!', likesCount: '15'},
                {id: 3, message: "it's my first post", likesCount: '50'},
                {id: 4, message: 'Hi!', likesCount: '20'},
                {id: 5, message: 'Hi!', likesCount: '300'},
                {id: 6, message: 'what is your name?', likesCount: '123'},
                {id: 7, message: 'Hi!', likesCount: '79'},
                {id: 8, message: 'Hi!', likesCount: '26'},
            ],
            newPostText: ''
        },
        messagesPage: {
            dialogsData: [
                {id: 1, name: 'Alex'},
                {id: 2, name: 'Nikita'},
                {id: 3, name: 'Mary'},
                {id: 4, name: 'Juli'},
                {id: 5, name: 'Valera'},
                {id: 6, name: 'Vsevolod'},
                {id: 7, name: 'Sergey'},
                {id: 8, name: 'Linda'},
            ],
            messagesData: [
                {id: 1, message: 'Hi!'},
                {id: 2, message: 'How are you?'},
                {id: 3, message: 'Yo!'},
                {id: 4, message: 'Ok!'},
                {id: 5, message: 'I like juice'},
                {id: 6, message: 'Do you like an ice-cream?"'},
                {id: 7, message: 'Foo!'},
                {id: 8, message: 'Are you crazy!!!'},
            ],
            newMessageText: ''
        },
        sidebar: {
            friends: [
                {id: 1, name: 'Andrew'},
                {id: 2, name: 'Sasha'},
                {id: 3, name: 'Svetlana'},
            ],
        },
    },
    _callSubscriber() {console.log('State was changed')}, //renderEntireTree()
    _addPost() {
        let newPost = {
            id: 9,
            message: this._state.profilePage.newPostText,
            likesCount: '0',
        };
        this._state.profilePage.postsData.push(newPost);
        this._state.profilePage.newPostText = '';
        this._callSubscriber(this._state);
    },
    _updateNewPostText(newText) {
        this._state.profilePage.newPostText = newText;
        this._callSubscriber(this._state);
    },
    _addMessage() {
        let newMessage = {
            id: 9,
            message: this._state.messagesPage.newMessageText,
        };
        this._state.messagesPage.messagesData.push(newMessage);
        this._state.messagesPage.newMessageText = '';
        this._callSubscriber(this._state);
    },
    _updateNewMessageText(newMessage) {
        this._state.messagesPage.newMessageText = newMessage;
        this._callSubscriber(this._state);
    },
    getState() {return this._state},
    subscribe(observer) {this._callSubscriber =  observer},
    dispatch(action) {
        if (action.type === 'ADD-POST') {
            this._addPost();
        } else if (action.type === 'UPDATE-NEW-POST-TEXT') {
            this._updateNewPostText(action.newText);
        }
        if (action.type === 'ADD-MESSAGE') {
            this._addMessage();
        } else if (action.type === 'UPDATE-NEW-MESSAGE-TEXT') {
            this._updateNewMessageText(action.newMessage);
        }
    }
}

export default store;
window.store = store; // can delete, only for control